import { Component, OnInit } from '@angular/core';
import { VisualData } from '../../shared/visualdata.service';
import { namevalueis, namevalue } from '../../bean/namevalue';

@Component({
  selector: 'app-enterprise-chart',
  templateUrl: './enterprise-chart.component.html',
  styleUrls: ['../position-chart/position-chart.component.css']
})
export class EnterpriseChartComponent implements OnInit {
  /* 顶部图表 */
  option: any;
  /* 图表占位对象 */
  eTempt: any;
  /* 图表类型 */
  flag: number = 1;
  constructor(
    private vd: VisualData
  ) {
    this.setDefaultEchart();
  }

  ngOnInit() { }
  /* 点击按钮切换 图表 */
  changeOption(num: number) {
    switch (num) {
      /* 公司数量与城市（饼状图 -> 南丁格尔图） compcitynum */
      case 0:
        this.vd.getCompCityNum().subscribe(
          data => {
            let companyNumCity: namevalueis[] = [];
            companyNumCity = data['result'].compcitynum;
            this.eTempt = {
              title: {
                text: '公司数量与城市',
                subtext: "根据公司信息计算所得",
                left: 'center',
                top: 10,
                textStyle: {
                  color: '#ddd'
                }
              },
              tooltip: {
                trigger: 'item',
                formatter: "{b} <br/> {c} ({d}%)"
              },
              visualMap: {
                show: false,
                min: 80,
                max: 600,
                inRange: {
                  colorLightness: [0, 1]
                }
              },
              series: [
                {
                  name: '公司数量与城市',
                  type: 'pie',
                  radius: '55%',
                  center: ['50%', '58%'],
                  data: companyNumCity.sort(function (a, b) { return a.value - b.value; }),
                  roseType: 'radius',
                  itemStyle: {
                    normal: {
                      borderWidth: 10,
                      borderColor: '#2d3548',
                    },
                    emphasis: {
                      borderWidth: 0,
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                  }
                }
              ]
            };
          }
        );
        break;
      /* 公司类型占比 (饼状图) */
      case 1:
        this.vd.getCompLocation().subscribe(
          data => {
            let majortype: namevalueis[] = [];
            majortype = data['result'].comlocation;
            this.eTempt = {
              title: {
                text: '公司类型占比',
                subtext: "根据公司信息计算所得",
                left: 'center',
                top: 10,
                textStyle: {
                  color: '#ddd'
                }
              },
              tooltip: {
                trigger: 'item',
                formatter: "{b} <br/> {c} <br/>({d}%)",
              },
              color: ['#ad46f3', '#5045f6', '#4777f5', '#44aff0', '#45dbf7', '#f6d54a', '#f69846', "#ff4343",],
              series: [{
                name: '库存情况',
                type: 'pie',
                radius: '68%',
                center: ['50%', '50%'],
                clockwise: false,
                data: majortype,
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                itemStyle: {
                  normal: {
                    borderWidth: 10,
                    borderColor: '#2d3548',
                  },
                  emphasis: {
                    borderWidth: 0,
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }],
            };
          }
        );
        break;
      /* 公司分布图 （饼状图 -> 南丁格尔图） */
      case 2:
        this.vd.getCompType().subscribe(
          data => {
            let majortype: namevalueis[] = [];
            majortype = data['result'].comtype;
            this.eTempt = {
              title: {
                text: '公司分布',
                subtext: "根据公司信息计算所得",
                left: 'center',
                top: 10,
                textStyle: {
                  color: '#ddd'
                }
              },
              tooltip: {
                trigger: 'item',
                formatter: "{b} <br/> {c} ({d}%)",
              },
              color: ['#ad46f3', '#5045f6', '#4777f5', '#44aff0', '#45dbf7', '#f6d54a', '#f69846', "#ff4343",],
              series: [{
                name: '库存情况',
                type: 'pie',
                radius: '68%',
                center: ['50%', '50%'],
                clockwise: false,
                data: majortype,
                roseType: 'radius',
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                itemStyle: {
                  normal: {
                    borderWidth: 10,
                    borderColor: '#2d3548',
                  },
                  emphasis: {
                    borderWidth: 0,
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }],
            };
          }
        )
        break;
      /*  TODO: 左侧一个折线图 右侧两个圆环图 undo 城市和比列 -> 在各个城市的数量： 上海有20家创业公司，30家上市公司 */
      case 3:
        this.vd.getCompTypeCity().subscribe(
          data => {
            let pinplace: string[] = [];
            let xConpPlace: string[] = [];
            let iDataCityData: namevalue[];
            let datasalary: Object[] = [];
            let datajobnumber: Object[] = [];

            // 城市 与 公司类型数量
            iDataCityData = data['result'].comptypecity.datacitydata;
            // 薪资 和 数量
            datasalary = data['result'].comptypecity.datasalary;
            // 城市
            xConpPlace = data['result'].comptypecity.comppalce;
            // 职业 与 数量
            datajobnumber = data['result'].comptypecity.datajobnum;
            this.eTempt = {
              color: ['#ffd285', '#ff733f', '#ec4863'],
              title: [{
                text: '公司城市分布',
                subtext: "根据公司信息计算所得",
                left: '1%',
                bottom: '1%',
                textStyle: {
                  color: '#ddd'
                }
              }, {
                text: '工资分布',
                left: '83%',
                top: '6%',
                textAlign: 'center',
                textStyle: {
                  color: '#fff'
                }
              },
              {
                text: '职位分布',
                left: '83%',
                top: '56%',
                textAlign: 'center',
                textStyle: {
                  color: '#fff'
                }
              }
              ],
              tooltip: {
                trigger: 'axis'
              },
              legend: {
                x: 300,
                top: '7%',
                textStyle: {
                  color: '#ffd285',
                },
                data: pinplace
              },
              grid: {
                left: '1%',
                right: '35%',
                top: '16%',
                bottom: '5%',
                containLabel: true
              },
              xAxis: {
                type: 'category',
                "axisLine": {
                  lineStyle: {
                    color: '#FF4500'
                  }
                },
                "axisTick": {
                  "show": false
                },
                axisLabel: {
                  rotate: 50,
                  textStyle: {
                    color: '#fff'
                  }
                },
                boundaryGap: false,
                data: xConpPlace,
              },
              yAxis: {
                "axisLine": {
                  lineStyle: {
                    color: '#fff'
                  }
                },
                splitLine: {
                  show: true,
                  lineStyle: {
                    color: '#fff'
                  }
                },
                "axisTick": {
                  "show": false
                },
                axisLabel: {
                  textStyle: {
                    color: '#fff'
                  }
                },
                type: 'value'
              },
              series: [
                {
                  name: iDataCityData[0].name,
                  smooth: true,
                  type: 'line',
                  symbolSize: 8,
                  symbol: 'circle',
                  data: iDataCityData[0].value
                }, {
                  name: iDataCityData[1].name,
                  smooth: true,
                  type: 'line',
                  symbolSize: 8,
                  symbol: 'circle',
                  data: iDataCityData[1].value
                }, {
                  name: iDataCityData[2].name,
                  smooth: true,
                  type: 'line',
                  symbolSize: 8,
                  symbol: 'circle',
                  data: iDataCityData[2].value
                },
                // 右侧圆环图
                {
                  name: 'tt',
                  tooltip: {
                    trigger: 'item',
                    formatter: " {b}: <br/>{c} <br/>({d}%)"
                  },
                  // 位置
                  center: ['83%', '30%'],
                  type: 'pie',
                  radius: ['25%', '30%'],
                  avoidLabelOverlap: false,
                  label: {
                    normal: {
                      show: true,
                      position: 'outside'
                    },
                    emphasis: {
                      show: true,
                      textStyle: {
                        fontWeight: 'bold'
                      }
                    }
                  },
                  labelLine: {
                    normal: {
                      show: true,
                      length: 6,
                      length2: 6
                    }
                  },
                  data: datasalary
                },
                // 以上构成一个扇形图片
                {
                  name: 'tt',
                  // 位置
                  center: ['83%', '80%'],
                  type: 'pie',
                  radius: ['25%', '30%'],
                  avoidLabelOverlap: false,
                  tooltip: {
                    trigger: 'item',
                    formatter: " {b}: <br/>{c} ({d}%)"
                  },
                  label: {
                    normal: {
                      show: true
                    },
                    emphasis: {
                      show: true,
                      textStyle: {
                        fontWeight: 'bold'
                      }
                    }
                  },
                  labelLine: {
                    normal: {
                      show: true,
                      length: 6,
                      length2: 6
                    }
                  },
                  data: datajobnumber
                }]
            }
          }
        );
        break;
      /* TODO: undo x职位 y数量 线条代表 城市（招聘数量最多的三个城市） */
      case 4:
      console.log("enterprise num is error");
        break;
      /* 顶部 公司规模和人才需求量（柱状图）TODO: 不能突出该主题 */
      default:
      console.log("enterprise num is error");
        break;
    }
  }

  /* 默认加载图表 */
  setDefaultEchart() {
    /* 顶部 公司规模和人才需求量（柱状图） */
    this.vd.getCompNum().subscribe(
      data => {
        let xScale: string[] = [];
        let yScaleNeed: number[] = [];
        let iCompScale = data['result'].compscalenum;
        for (let i = 0; i < iCompScale.length; i++) {
          xScale.push(iCompScale[i].name);
          yScaleNeed.push(iCompScale[i].value);
        }
        this.option = {
          // backgroundColor: '#011c3a',
          legend: {
            data: ["公司规模"],
            right: '5%',
            textStyle: {
              color: "#fff"
            }
          },
          xAxis: [{
            data: xScale,
            axisLine: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#aaa'
              }
            },
            axisTick: {
              show: false
            }
          }],
          yAxis: {
            ayisLine: {
              show: true
            },
            axisLabel: {
              textStyle: {
                color: '#aaa'
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: '#2e3547'
              }
            },
            axisLine: {
              show: false,
            },

          },
          series: [{
            name: '公司规模',
            type: 'bar',
            barWidth: 18,
            itemStyle: {
              normal: {
                "color": {
                  "type": "linear",
                  "x": 0,
                  "y": 0,
                  "x2": 0,
                  "y2": 1,
                  "colorStops": [{
                    offset: 0,
                    color: '#821eff'
                  },

                  {
                    offset: 1,
                    color: '#204fff'
                  }
                  ]
                },
                shadowBlur: 10,
              }
            },
            data: yScaleNeed,
          }]
        };
      }
    );
    /* 底部默认 图表 */
    this.changeOption(0);
  }

}



