import { Component, OnInit } from '@angular/core';
import * as echarts from 'echarts';
import { VisualData } from '../../shared/visualdata.service';
import { namevalueis } from '../../bean/namevalue';

@Component({
  selector: 'app-job-seeker-chart',
  templateUrl: './job-seeker-chart.component.html',
  styleUrls: ['../position-chart/position-chart.component.css']
})
export class JobSeekerChartComponent implements OnInit {

  option3: any;
  option: any;
  userData: any;

  /* 默认显示图表 用户学历&工作经验 */
  eUserEdu: any;
  eUserExper: any;


  /* 切换 两个各个按钮 */
  flag = 0;
 


  data = [
    {
      value: 45,
      name: 'CARD'
    }, {
      value: 25,
      name: 'SSD'
    }, {
      value: 15,
      name: 'U盘'
    }, {
      value: 8,
      name: '嵌入式'
    }, {
      value: 7,
      name: 'FLASH'
    }
  ];
  constructor(
    private vd: VisualData
  ) {
    this.option3 = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          lineStyle: {
            color: '#57617B'
          }
        }
      },
      legend: {
        data: ['人工智能热度指数'],
        right: '4%',
        textStyle: {
          fontSize: 12,
          color: '#F1F1F3'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [{
        type: 'category',
        boundaryGap: false,
        axisTick: {
          show: false
        },
        axisLine: {
          show: false
        },
        axisLabel: {
          textStyle: {
            color: '#aaa'
          }
        },
        data: ['2017-10-01', '2017-10-02', '2017-10-03', '2017-10-04', '2017-10-05', '2017-10-06', '2017-10-07', '2017-10-08', '2017-10-09', '2017-10-10', '2017-10-11', '2017-10-12']
      }],
      yAxis: [{
        type: 'value',
        name: '',
        axisTick: {
          show: false
        },
        axisLine: {
          show: false
        },
        axisLabel: {
          margin: 10,
          textStyle: {
            color: '#aaa'
          }
        },
        splitLine: {
          lineStyle: {
            color: '#2f364acc'
          }
        }
      }],
      series: [{
        name: '人工智能热度指数',
        type: 'line',
        smooth: true,
        symbol: 'circle',
        symbolSize: 5,
        showSymbol: false,
        lineStyle: {
          normal: {
            width: 1
          }
        },
        areaStyle: {
          normal: {
            color: {
              "type": "linear",
              "x": 0,
              "y": 0,
              "x2": 0,
              "y2": 1,
              "colorStops": [
                {
                  offset: 0,
                  color: 'rgba(0, 136, 212, 0.3)'
                },
                {
                  offset: 0.8,
                  color: 'rgba(0, 136, 212, 0)'
                }
              ]
            },
            shadowColor: 'rgba(0, 0, 0, 0.1)',
            shadowBlur: 10
          }
        },
        itemStyle: {
          normal: {
            color: 'rgb(0,136,212)',
            borderColor: 'rgba(0,136,212,0.2)',
            borderWidth: 12

          }
        },
        data: [120, 110, 125, 145, 122, 165, 122, 220, 182, 191, 134, 150]
      },]
    };
    /* 默认展示 */
    this.defaultShowEcharts()
}

  ngOnInit() {}

  changeOption(num: number){
    console.log("Clike glyphicon : " + num);
    /* 默认显示一张图表 */
    this.flag = 1;
    switch (num) {
      /* 用户学历（圆环图）&工作经验（饼图） */
      case 0:
        this.flag = 0;
        this.vd.getUserData().subscribe(
          data => {
            console.log("用户个人信息");
            console.log(data['result'].userEdu);
            console.log(data['result'].userExper);
            if (data['status'] == 'ok') {
              let userEdu: namevalueis[];
              let userExper: namevalueis[];

              userEdu = data['result'].userEdu;


              userExper = data['result'].userExper;


              this.eUserExper = {
                title: {
                  text: '用户经验',
                  subtext: '根据用户信息分析所得',
                  left: 'center',
                  textStyle: {
                    color: "#ddd"
                  }
                },
                tooltip: {
                  trigger: 'item',
                  formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                series: [{
                  name: '库存情况',
                  type: 'pie',
                  radius: [0, 120],
                  center: ['50%', '50%'],
                  clockwise: false,
                  data: userExper,
                  label: {
                    normal: {
                      show: true,
                      textStyle: {
                        color: '#999',
                        fontSize: 14,
                      }
                    }
                  },
                  itemStyle: {
                    emphasis: {
                      borderWidth: 0,
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                  }
                }],
                color: ['#ad46f3', '#44aff0', '#f6d54a', '#f69846', '#6f81da', '#ad46f3'],

              };
              this.eUserEdu = {
                title: {
                  text: '用户教育经历',
                  subtext: '根据用户信息分析所得',
                  left: 'center',
                  textStyle: {
                    color: "#ddd"
                  }
                },
                tooltip: {
                  trigger: 'item',
                  formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                series: [{
                  name: '库存情况',
                  type: 'pie',
                  radius: [100, 120],
                  center: ['50%', '50%'],
                  clockwise: false,
                  data: userExper,
                  label: {
                    normal: {
                      show: true,
                      textStyle: {
                        color: '#999',
                        fontSize: 14,
                      }
                    }
                  },
                  itemStyle: {
                    emphasis: {
                      borderWidth: 0,
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                  }
                }],
                color: ['#ad46f3', '#44aff0', '#f6d54a', '#f69846', '#6f81da', '#ad46f3'],
              };
            }
          }
        )
        break;
      /* 用户年龄分布（折线图） */
      case 1:
        this.vd.getUserAgeNum().subscribe(
          data => {
            let xAge: string[] = [];
            let yNum: number[] = [];
            const temp = data['result'].userage;
            for (let i = 0; i < temp.length; i++) {
              xAge.push(temp[i].name);
              yNum.push(temp[i].value);
            }
            console.log("求职者年龄分布图");
            console.log(xAge);
            console.log(yNum);
            this.option = {
              // backgroundColor: "#0f375f",
              title: {
                text: '求职者年龄分布图',
                subtext: "根据用户信息分析所得",
                left: 'center',
                // top: 10,
                textStyle: {
                  color: '#fff'
                },
                x: 'center'
              },
              color: ["#036BC8", "#4A95FF", "#5EBEFC", "#2EF7F3", "#FFFFFF"],
              tooltip: {
                trigger: 'axis',
                formatter: "年龄区间： {b}<br/> 数量：{c} "
              },
              xAxis: {
                type: 'category',
                // 突破Y轴最大值
                boundaryGap: false,
                data: xAge,
                axisLine: { show: false, lineStyle: { color: '#6173A3' } },
                axisLabel: {
                  formatter: '{value} 岁',
                  interval: 0,
                  margin: 10,
                  rotate: 40,
                  textStyle: {
                    color: '#999'
                  }
                }, axisTick: {
                  "show": false
                },
              },
              yAxis: {
                type: 'value',
                axisLine: { show: false, lineStyle: { color: '#6173A3' } },
                splitLine: {
                  show: true,
                  lineStyle: {
                    color: '#363f54'
                  }
                },
                axisTick: {
                  "show": false
                },
                axisLabel: {
                  formatter: '{value}',
                  textStyle: {
                    color: '#999'
                  }
                }
              },
              series: [{
                name: '',
                type: 'line',
                data: yNum,
                symbol: 'circle',
                symbolSize: 10,
                lineStyle: {
                  normal: {
                    color: '#f6d54a',
                    type: 'dotted'
                  }
                },
                itemStyle: {
                  normal: {
                    borderWidth: 3,
                    borderColor: '#f00',
                    color: '#fff'
                  }
                }
              }]
            };
          }
        )
        break;
      /* 用户偏爱岗位(折线图) */
      case 2:
        this.vd.getUserFavJob().subscribe(
          data => {
            let iJob = data['result'].favJob;
            /* 最受求职者欢迎的岗位 x 岗位 y 数量 */
            let xJob: string[] = [];
            let yJobNum: number[] = [];
            for (let k = 0; k < iJob.length; k++) {
              xJob.push(iJob[k].name);
              yJobNum.push(iJob[k].value);
            }
            this.option = {
              // backgroundColor: '#293042',
              tooltip: {
                show: true,
                trigger: 'item'
              },
              title: {
                text: "求职者喜爱岗位",
                subtext: "根据搜索分析所得",
                x: 'center',
                top: 10,
                textStyle: {
                  color: "#fff"
                }
              },
              grid: {
                left: '4%',
                top: '25%',
                bottom: '1%',
                containLabel: true
              },
              xAxis: {
                type: 'category',
                boundaryGap: true,
                axisTick: {
                  show: false
                },
                axisLine: {
                  lineStyle: {
                    color: '#6995aa'
                  },
                  onZero: true
                },
                axisLabel: {
                  fontSize: 11,
                  margin: 10,
                  color: '#eee',
                  interval: 0,
                  rotate: 25
                },
                splitLine: {
                  show: false
                },
                data: xJob
              },
              yAxis: {
                type: 'value',
                axisTick: {
                  show: false
                },
                axisLine: {
                  lineStyle: {
                    color: '#6995aa'
                  },
                  onZero: false
                },
                axisLabel: {
                  fontSize: 11,
                  color: '#eee'
                },
                splitLine: {
                  lineStyle: {
                    type: 'dotted',
                    color: '#1978D9'
                  }
                },
                scale: true
              },
              series: [{
                name: '第一产业',
                smooth: true,
                type: 'line',
                symbolSize: 5,
                symbol: 'circle',
                itemStyle: {
                  normal: {
                    label: {
                      formatter: function (params) {
                        return 100 - params.value;
                      },
                      fontSize: 40,
                      padding: [90, 0, 0, 0],
                      color: '#fff',
                      textStyle: {
                        baseline: 'top'
                      }
                    },
                    color: '#ffea00',
                    borderColor: 'rgba(255, 234, 0, 0.5)',
                    borderWidth: 10
                  }
                },
                data: yJobNum
              }]
            };
          }
        );
        break;
      /* 用户喜爱城市(饼图) */
      case 3:
        /* TODO: 2-间距 3-竖直方向线条*/
        this.vd.getUserFavCity().subscribe(
          data => {
            let userFavCity: Object[] = [];
            userFavCity = data['result'].favCity;
            console.log("求职者喜爱城市");
            console.log(userFavCity);
            this.option = {
              // backgroundColor: "#eee",
              title: {
                text: '求职者喜爱城市',
                subtext: '根据搜索分析所得',
                left: 'center',
                top: 20,
                textStyle: {
                  color: '#fff'
                },
              },
              color: ['#ad46f3', '#44aff0', '#f6d54a', '#f69846', '#6f81da', '#ad46f3'],              
              tooltip: {
                trigger: 'item',
                formatter: "{b}<br/> {c} ({d}%)"
              },
              calculable: true,
              series: [
                {
                  name: 'tt',
                  type: 'pie',
                  radius: '55%',
                  center: ['50%', '60%'],
                  data: userFavCity,
                }
              ]
            };
          }
        )
        break;
    }
  }

  /* 默认加载 eUserEdu eUserExper 显示 或者显示 两个图表 */
  defaultShowEcharts(){
    /* 底部 图表 */
    this.changeOption(0);

    /* 个人能力图谱 */
    var radarDataAvg = [14, 17, 18, 16, 15, 10, 12];
    var radarData = [20, 20, 2, 20, 20, 20, 20];
    // 颜色设置
    var color = {
      linearYtoG: {
        type: 'linear',
        x: 0,
        y: 0,
        x2: 1,
        y2: 1,
        colorStops: [{
          offset: 0,
          color: '#f5b44d'
        }, {
          offset: 1,
          color: '#28f8de'
        }]
      },
      linearGtoB: {
        type: 'linear',
        x: 0,
        y: 0,
        x2: 1,
        y2: 0,
        colorStops: [{
          offset: 0,
          color: '#43dfa2'
        }, {
          offset: 1,
          color: '#28f8de'
        }]
      },
      linearBtoG: {
        type: 'linear',
        x: 0,
        y: 0,
        x2: 1,
        y2: 0,
        colorStops: [{
          offset: 0,
          color: '#1c98e8'
        }, {
          offset: 1,
          color: '#28f8de'
        }]
      },
      areaBtoG: {
        type: 'linear',
        x: 0,
        y: 0,
        x2: 0,
        y2: 1,
        colorStops: [{
          offset: 0,
          color: 'rgba(35,184,210,.2)'
        }, {
          offset: 1,
          color: 'rgba(35,184,210,0)'
        }]
      }
    };
    this.userData = {
      title: {
        text: '我的能力值',
        textStyle: {
          color: '#fff',
          fontSize: 32,
          fontWeight: 'normal'
        },
        subtext: 2018 + '/0407',
        subtextStyle: {
          color: '#fff',
          fontSize: 16,
        },
        top: 50,
        left: 80
      },
      legend: {
        top: 220,
        left: 80,
        orient: 'vertical',
        itemGap: 15,
        itemWidth: 12,
        itemHeight: 12,
        data: ['平均指标', '我的指标'],
        textStyle: {
          color: '#fff',
          fontSize: 14,
        },
      },
      tooltip: {
        trigger: 'item'
      },
      radar: {
        center: ['68%', '27%'],
        radius: '40%',
        name: {
          color: '#fff'
        },
        splitNumber: 8,
        axisLine: {
          lineStyle: {
            color: color.linearYtoG,
            opacity: .6
          }
        },
        splitLine: {
          lineStyle: {
            color: color.linearYtoG,
            opacity: .6
          }
        },
        splitArea: {
          areaStyle: {
            color: '#fff',
            opacity: .1,
            shadowBlur: 25,
            shadowColor: '#000',
            shadowOffsetX: 0,
            shadowOffsetY: 5,
          }
        },
        indicator: [{
          name: '全程距离(m)',
          max: 30
        }, {
          name: '平均速度(km/h)',
          max: 30
        }, {
          name: '最快速度(km/h)',
          max: 30
        }, {
          name: '总计时间(h)',
          max: 30
        }]
      },
      grid: {
        left: 90,
        right: 80,
        bottom: 40,
        top: '60%',
      },
      series: [{
        name: '每日跑步指标分布与比较',
        type: 'radar',
        symbolSize: 0,
        data: [{
          value: radarDataAvg,
          name: '平均指标',
          itemStyle: {
            normal: {
              color: '#f8d351',
            }
          },
          lineStyle: {
            normal: {
              opacity: 0,
            }
          },
          areaStyle: {
            normal: {
              color: '#f8d351',
              shadowBlur: 25,
              shadowColor: 'rgba(248,211,81,.3)',
              shadowOffsetX: 0,
              shadowOffsetY: -10,
              opacity: 1
            }
          },
        }, {
          value: radarData,
          name: '我的指标',
          itemStyle: {
            normal: {
              color: '#43dfa2',
            }
          },
          lineStyle: {
            normal: {
              opacity: 0,
            }
          },
          areaStyle: {
            normal: {
              color: color.linearGtoB,
              shadowBlur: 15,
              shadowColor: 'rgba(0,0,0,.2)',
              shadowOffsetX: 0,
              shadowOffsetY: 5,
              opacity: .8
            }
          },
        }]
      }],
      backgroundColor: '#383546',
    };
  }

}



