import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { ReactiveFormsModule } from '@angular/forms';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { HttpClientModule} from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { CarouselComponent } from './carousel/carousel.component';
import { NavbarComponent } from './navbar/navbar.component';
import { ProductService } from './shared/product.service';
import { FilterPipe } from './pipe/filter.pipe';
import { CookieModule } from 'ngx-cookie';
import { AppRoutingModule} from './app-routing.module';
import { PersonalCenterModule} from './personal-center/personal-center.module';
import { NopageComponent } from './nopage/nopage.component';
import { RegisterComponent } from './register/register.component';
import { RecommendComponent } from './recommend/recommend.component';
import { HotJobsComponent } from './hot-jobs/hot-jobs.component';
import { PersonalCenterComponent } from './personal-center/personal-center.component';
import { PersonalizedRecommendationComponent} from './personalized-recommendation/personalized-recommendation.component';
import { FooterComponent } from './footer/footer.component';
import { MycookieService } from './shared/mycookie.service';
import { ModalModule } from 'ngx-bootstrap/modal';
import { AuthGuard } from './shared/can-active-guard';
import { AuthService } from './shared/auth.service';
import { SigninComponent } from './signin/signin.component';
import { ModalComponent } from './modal/modal.component';
import { ModalService } from './shared/modal-service';
import { UserAsyncDirective } from './validators/UserMobileDirective ';
import { SearchHomeComponent } from './search-home/search-home.component';
import { ChartsModule } from './charts/ChartsModule';
import { VisualData } from './shared/visualdata.service';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    CarouselComponent,
    
    NavbarComponent,
    FilterPipe,
    NopageComponent,
    RegisterComponent,
    RecommendComponent,
    HotJobsComponent,
    PersonalCenterComponent,
    PersonalizedRecommendationComponent,
    FooterComponent,    
    SigninComponent,
    ModalComponent,
    UserAsyncDirective,
    SearchHomeComponent,
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    PersonalCenterModule,
    ChartsModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    HttpClientModule,
    CookieModule.forRoot(),
    ModalModule.forRoot()
  ],
  providers: [
    ProductService,
    {provide: LocationStrategy, useClass: HashLocationStrategy},
    MycookieService,
    AuthGuard,
    AuthService,
    ModalService,
    VisualData
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    SigninComponent,
    ModalComponent,
    RegisterComponent
  ]
})
export class AppModule {}



