import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { ChartsRouting } from './ChartsRouting';
import { NgxEchartsModule } from 'ngx-echarts';
import { PositionChartComponent } from './position-chart/position-chart.component';
import { JobSeekerChartComponent } from './job-seeker-chart/job-seeker-chart.component';
import { ChartsComponent } from './charts.component';
import { EnterpriseChartComponent } from './enterprise-chart/enterprise-chart.component';

@NgModule({
    imports: [
        BrowserModule,
        ChartsRouting,
        ReactiveFormsModule,
        FormsModule,
        CommonModule, 
        NgxEchartsModule
    ],
    declarations: [
        PositionChartComponent,
        JobSeekerChartComponent,
        ChartsComponent,
        EnterpriseChartComponent
    ]
})

export class ChartsModule { }



