import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { ChartsComponent } from './charts.component';
import { PositionChartComponent } from './position-chart/position-chart.component';
import { JobSeekerChartComponent } from './job-seeker-chart/job-seeker-chart.component';
import { EnterpriseChartComponent } from './enterprise-chart/enterprise-chart.component';



const routeConfig: Routes = [{
    path: 'charts',
    component: ChartsComponent,
    children: [
        { path: 'position-charts', component: PositionChartComponent },
        { path: 'jobseeker-charts', component: JobSeekerChartComponent },
        { path: 'enterprise-charts', component: EnterpriseChartComponent },
        { path: '', redirectTo: '/charts/jobseeker-charts', pathMatch: 'full' },
    ]
}
]

@NgModule({
    imports: [
        RouterModule.forChild(
            routeConfig
        )
    ],
    exports: [
        RouterModule
    ]
})

export class ChartsRouting { }



